import classes from "./Backup.module.css"
import { faClipboard, faUpload, faDownload, faTrash, faFileImport, faFileCode } from "@fortawesome/free-solid-svg-icons"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { useLocalStorage } from "../../../Utils"
import React, { useState, useCallback, useMemo } from "react"
import Modal from "../../../components/UI/Modal/Modal"
import headData from "../../../assets/json/parameters/GearInfoHead.json"
import clothesData from "../../../assets/json/parameters/GearInfoClothes.json"
import shoesData from "../../../assets/json/parameters/GearInfoShoes.json"
import { SkillIDsRev } from "../../../Utils"
import { useBoolState, dict_length } from "../../../Utils"
import { useTranslation } from "react-i18next"
import { defaultGearList, defaultImportData } from "../../../DefaultEntries"

const Backup = props => {
    const { t } = useTranslation()

    const [gearList, setGearList] = useLocalStorage("gearList", defaultGearList)
    const [userdata, setUserData] = useLocalStorage("importData", defaultImportData)
    const [uploadOpen, onOpen, onClose] = useBoolState()

    const get_full_data_struct = useCallback(() => {
        return {
            HaveGearHeadMap: gearList.HaveGearHeadMap,
            HaveGearShoesMap: gearList.HaveGearShoesMap,
            HaveGearClothesMap: gearList.HaveGearClothesMap,
            CloudRandomSet: userdata.CloudRandomSet,
            UserKey: userdata.UserKey,
            H: userdata.H,
        }
    }, [gearList, userdata])

    const copyToClipboard = useCallback(
        () =>
            navigator.clipboard
                .writeText(JSON.stringify(get_full_data_struct()))
                .then(() => alert(t("misc:Common.CopyToClipboard")))
                .catch(console.error),
        [t, get_full_data_struct]
    )

    const deleteData = useCallback(() => {
        if (!window.confirm(t("misc:Common.DeleteConfirmation"))) return

        setGearList(defaultGearList)
        setUserData(defaultImportData)
    }, [setGearList, t, setUserData])

    const downloadData = useCallback(() => {
        const date = new Date()
        const dateStr = date.toISOString().split(".")[0].replace("T", "_").replaceAll(":", "-")
        const JSONStr = JSON.stringify(get_full_data_struct())
        const filename = `gear_seed_database_${dateStr}.json`
        const contentType = "application/json;charset=utf-8"
        const a = document.createElement("a")
        a.download = filename
        a.href = `data:${contentType},${encodeURIComponent(JSONStr)}`
        a.target = "_blank"
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
    }, [get_full_data_struct])

    const onUpload = useCallback(
        newDB => {
            setGearList(newDB.GearDB)
            setUserData(newDB.UserData)
            onClose()
        },
        [onClose, setGearList, setUserData]
    )

    const hasData =
        dict_length(gearList.HaveGearHeadMap) > 0 ||
        dict_length(gearList.HaveGearClothesMap) > 0 ||
        dict_length(gearList.HaveGearShoesMap) > 0 > 0

    const headSeeds = Object.assign(
        {},
        ...Object.entries(gearList.HaveGearHeadMap)
            .filter(([k, v]) => v.RandomContext > 0)
            .map(([k, v]) => ({ [k]: v }))
    )
    const clothesSeeds = Object.assign(
        {},
        ...Object.entries(gearList.HaveGearClothesMap)
            .filter(([k, v]) => v.RandomContext > 0)
            .map(([k, v]) => ({ [k]: v }))
    )
    const shoesSeeds = Object.assign(
        {},
        ...Object.entries(gearList.HaveGearShoesMap)
            .filter(([k, v]) => v.RandomContext > 0)
            .map(([k, v]) => ({ [k]: v }))
    )

    const foundSeeds = dict_length(headSeeds) + dict_length(clothesSeeds) + dict_length(shoesSeeds)

    return (
        <>
            <div className={classes.cardWrapperWrapper}>
                <div className={classes.cardWrapper}>
                    <div className={classes.Subtitle}>
                        <h6 className={classes.Subtitleh6}>{t("misc:Common.Database")}</h6>
                    </div>
                    <hr className={classes.Divider} />
                    <div className={classes.CardContent}>
                        <div className={classes.CardDark}>
                            <div>
                                <div>
                                    <p className={classes.TextDescription}>
                                        {t("translation:LayoutMsg.Cmn_CstBase_00.L_BtnCategory_01-T_Category_00")}:{" "}
                                        <strong>{dict_length(gearList.HaveGearHeadMap)}</strong>
                                    </p>
                                    <p className={classes.TextDescription}>
                                        {t("translation:LayoutMsg.Cmn_CstBase_00.L_BtnCategory_02-T_Category_00")}:{" "}
                                        <strong>{dict_length(gearList.HaveGearClothesMap)}</strong>
                                    </p>
                                    <p className={classes.TextDescription}>
                                        {t("translation:LayoutMsg.Cmn_CstBase_00.L_BtnCategory_03-T_Category_00")}:{" "}
                                        <strong>{dict_length(gearList.HaveGearShoesMap)}</strong>
                                    </p>
                                    <p className={classes.TextDescription}>
                                        {t("misc:Common.SeedsFound")}: <strong>{foundSeeds}</strong>
                                    </p>
                                </div>
                                <div style={{ marginTop: 16 }}>
                                    <div className={classes.GridStyle}>
                                        <div className={classes.GridElem}>
                                            <button
                                                disabled={!hasData}
                                                onClick={copyToClipboard}
                                                className={hasData ? classes.ButtonInfo : classes.ButtonDisabled}
                                            >
                                                <FontAwesomeIcon icon={faClipboard} />{" "}
                                                <span style={{ padding: 6 }}>{t("misc:Common.Clipboard")}</span>
                                            </button>
                                        </div>
                                        <div className={classes.GridElem}>
                                            <Modal show={uploadOpen} modalClosed={onClose} wide={true}>
                                                <UploadCard onReplace={onUpload} />
                                            </Modal>
                                            <button onClick={onOpen} className={classes.ButtonInfo}>
                                                <FontAwesomeIcon icon={faUpload} />{" "}
                                                <span style={{ padding: 6 }}>{t("misc:Common.Upload")}</span>
                                            </button>
                                        </div>
                                        <div className={classes.GridElem}>
                                            <button
                                                disabled={!hasData}
                                                onClick={downloadData}
                                                className={hasData ? classes.ButtonDefault : classes.ButtonDisabled}
                                            >
                                                <FontAwesomeIcon icon={faDownload} />{" "}
                                                <span style={{ padding: 6 }}>{t("misc:Common.Download")}</span>
                                            </button>
                                        </div>
                                        <div className={classes.GridElem}>
                                            <button
                                                disabled={!hasData}
                                                onClick={deleteData}
                                                className={hasData ? classes.ButtonError : classes.ButtonDisabled}
                                            >
                                                <FontAwesomeIcon icon={faTrash} />{" "}
                                                <span style={{ padding: 6 }}>{t("misc:Common.Delete")}</span>
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

const UploadCard = props => {
    const { t } = useTranslation()

    const [data, setdata] = useState("")
    const [filename, setfilename] = useState("")
    const [errorMsg, setErrorMsg] = useState("")

    const onUpload = async e => {
        const file = e.target.files[0]
        e.target.value = null // reset the value so the same file can be uploaded again...
        if (file) setfilename(file.name)
        const reader = new FileReader()
        reader.onload = () => setdata(reader.result)
        reader.readAsText(file)
    }

    const importedDatabase = useMemo(() => {
        if (!data) return
        let parsed
        try {
            parsed = JSON.parse(data)
            if (typeof parsed !== "object") {
                setErrorMsg(t("misc:Common.ErrorJSONParse"))
                return
            }
        } catch (e) {
            setErrorMsg(t("misc:Common.ErrorJSONParse"))
            return
        }
        let tmp_data = parsed
        if ("gear" in tmp_data) {
            setErrorMsg("Please use the Import from SplatNet upload")
            return
        }
        if (Array.isArray(parsed)) {
            if (parsed.length === 2) {
                tmp_data = parsed[1]
            } else {
                setErrorMsg(t("misc:Database.ErrorInvalidFormat"))
                return
            }
        }
        let head, clothes, shoes
        if (
            !("HaveGearHeadMap" in tmp_data) ||
            !("HaveGearClothesMap" in tmp_data) ||
            !("HaveGearShoesMap" in tmp_data)
        ) {
            setErrorMsg(t("misc:Database.ErrorInvalidFormat"))
            return
        }
        head = tmp_data.HaveGearHeadMap
        clothes = tmp_data.HaveGearClothesMap
        shoes = tmp_data.HaveGearShoesMap

        const use_head = {}
        const use_clothes = {}
        const use_shoes = {}
        for (const [key, val] of Object.entries(head)) {
            const h = headData.filter(e => e.Id === parseInt(key))
            if (h.length > 0) {
                use_head[key] = {
                    ExSkillArray: val.ExSkillArray ?? [],
                    ExDrinksArray: val.ExDrinksArray ?? [],
                    MainSkill: val.MainSkill ?? SkillIDsRev[h.Skill],
                    RandomContext: val.RandomContext ?? 0,
                }
            }
        }
        for (const [key, val] of Object.entries(clothes)) {
            const h = clothesData.filter(e => e.Id === parseInt(key))
            if (h.length > 0) {
                use_clothes[key] = {
                    ExSkillArray: val.ExSkillArray ?? [],
                    ExDrinksArray: val.ExDrinksArray ?? [],
                    MainSkill: val.MainSkill ?? SkillIDsRev[h.Skill],
                    RandomContext: val.RandomContext ?? 0,
                }
            }
        }
        for (const [key, val] of Object.entries(shoes)) {
            const h = shoesData.filter(e => e.Id === parseInt(key))
            if (h.length > 0) {
                use_shoes[key] = {
                    ExSkillArray: val.ExSkillArray ?? [],
                    ExDrinksArray: val.ExDrinksArray ?? [],
                    MainSkill: val.MainSkill ?? SkillIDsRev[h.Skill],
                    RandomContext: val.RandomContext ?? 0,
                }
            }
        }
        setErrorMsg("")

        let user_res = defaultImportData

        if ("UserKey" in tmp_data && "H" in tmp_data) {
            user_res["UserKey"] = tmp_data.UserKey
            user_res["H"] = tmp_data.H
        }
        if ("CloudRandomSet" in tmp_data) {
            user_res["CloudRandomSet"] = tmp_data.CloudRandomSet
        }

        const res = {
            GearDB: {
                HaveGearHeadMap: use_head,
                HaveGearClothesMap: use_clothes,
                HaveGearShoesMap: use_shoes,
            },
            UserData: user_res,
        }
        return res
    }, [data, t])

    const replaceDB = useCallback(() => {
        if (!importedDatabase) return
        setdata("")
        setfilename("")
        props.onReplace(importedDatabase)
    }, [importedDatabase, props])

    return (
        <>
            <div className={classes.CardContent}>{t("misc:Common.DatabaseUpload")}</div>
            <div className={classes.CardContent}>
                <div className={classes.GridStyle}>
                    <div style={{ paddingLeft: 16, paddingTop: 16 }}>
                        <label>
                            <input
                                id="icon-button-file"
                                accept=".json"
                                type="file"
                                style={{ display: "none" }}
                                onChange={onUpload}
                            />

                            <span className={classes.ButtonDefault}>
                                <FontAwesomeIcon icon={faFileImport} style={{ marginRight: 8 }} />
                                {t("misc:Database.ButtonOpen")}
                            </span>
                        </label>
                    </div>
                    <div style={{ paddingLeft: 16, paddingTop: 16 }}>
                        <div style={{ paddingLeft: 16, paddingTop: 6 }}>
                            {filename ? (
                                <span>
                                    <FontAwesomeIcon icon={faFileCode} style={{ marginRight: 8 }} /> {filename}
                                </span>
                            ) : (
                                <span>{t("misc:Database.UploadJson")}</span>
                            )}
                        </div>
                    </div>
                </div>
                <div></div>
                <span className={classes.TextTiny}>{t("misc:Database.UploadJsonPath")}</span>
                <textarea value={data} onChange={e => setdata(e.target.value)}></textarea>
                {!importedDatabase ? (
                    errorMsg
                ) : (
                    <div style={{ marginTop: 16 }}>
                        <div>
                            <div>
                                {t("translation:LayoutMsg.Cmn_CstBase_00.L_BtnCategory_01-T_Category_00")}:{" "}
                                <strong>{dict_length(importedDatabase.GearDB.HaveGearHeadMap)}</strong>
                            </div>
                            <div>
                                {t("translation:LayoutMsg.Cmn_CstBase_00.L_BtnCategory_02-T_Category_00")}:{" "}
                                <strong>{dict_length(importedDatabase.GearDB.HaveGearClothesMap)}</strong>
                            </div>
                            <div>
                                {t("translation:LayoutMsg.Cmn_CstBase_00.L_BtnCategory_03-T_Category_00")}:{" "}
                                <strong>{dict_length(importedDatabase.GearDB.HaveGearShoesMap)}</strong>
                            </div>
                        </div>
                    </div>
                )}
            </div>
            <hr className={classes.Divider} />
            <div className={classes.CardContent}>
                <button
                    className={importedDatabase ? classes.ButtonInfo : classes.ButtonDisabled}
                    disabled={!importedDatabase}
                    onClick={replaceDB}
                >
                    <FontAwesomeIcon icon={faUpload} />{" "}
                    <span style={{ padding: 6 }}>{t("misc:Common.UpdateDatabase")}</span>
                </button>
            </div>
        </>
    )
}

export default Backup
