import React, { useState, Suspense, useCallback } from "react"
import classes from "./Database.module.css"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faPlus } from "@fortawesome/free-solid-svg-icons"
import Gear from "./Gear"
import gearHead from "../../assets/json/parameters/GearInfoHead.json"
import gearClothes from "../../assets/json/parameters/GearInfoClothes.json"
import gearShoes from "../../assets/json/parameters/GearInfoShoes.json"
import { useLocalStorage, getSkillId, getSkillName, decrypted_key, valid_key } from "../../Utils"
import MurmurHash3 from "murmurhash3js"
import { defaultGearList } from "../../DefaultEntries"
import { useTranslation } from "react-i18next"

const GearEditor = React.lazy(() => import("./GearEditor"))

const Database = props => {
    const { t } = useTranslation()
    const [gearList, setGearList] = useLocalStorage("gearList", defaultGearList)

    const [newGear, setNewGear] = useState(false)

    const [newGearId, setNewGearId] = useState(-1)
    const [newType, setNewGearType] = useState(-1)
    const [newMain, setNewMain] = useState("Unknown")
    const [newSeed, setNewSeed] = useState(0)
    const [newHistory, setNewHistory] = useState([])
    const [newDrinks, setNewDrinks] = useState([])

    const [loadAbility, setLoadAbility] = useState(-1)

    const newGearHandler = (gearId, gearType) => {
        setNewGearId(gearId)
        setNewGearType(gearType)
        const gearList = gearType === 0 ? gearHead : gearType === 1 ? gearClothes : gearShoes
        const pgear = gearList.filter(elem => elem.Id === gearId)
        setNewMain(pgear[0].Skill)
        setNewSeed(0)
    }

    const cancelGearEdit = () => {
        setNewGear(false)
        setNewGearId(-1)
        setNewGearType(-1)
        setNewMain("Unknown")
        setNewSeed(0)
        setLoadAbility(-1)
        setNewHistory([])
        setNewDrinks([])
    }

    const add_new_gear_handler = elem => {
        const gearKey = newType === 0 ? "HaveGearHeadMap" : newType === 1 ? "HaveGearClothesMap" : "HaveGearShoesMap"
        const new_dict = { ...gearList }
        new_dict[gearKey][newGearId] = {
            ExSkillArray: newHistory,
            ExDrinksArray: newDrinks,
            MainSkill: getSkillId(newMain),
            RandomContext: newSeed,
        }
        setGearList(new_dict)
        cancelGearEdit()
    }

    const set_new_gear_handler = (p_gear_id, p_type, p_main, p_history, p_historyDrinks, p_seed) => {
        setNewGearId(p_gear_id)
        setNewGearType(p_type)
        setNewMain(getSkillName(p_main))
        setNewSeed(p_seed)
        setNewHistory([...p_history])
        setNewDrinks([...p_historyDrinks])
        setNewGear(true)
    }

    const checkSeedHandler = event => {
        let inputstr = event.target.value.toUpperCase()
        if (inputstr.substring(0, 2) === "0X") {
            inputstr = inputstr.substring(2)
        }
        inputstr = inputstr.replace(/[^0-9A-F]/g, "")
        while (inputstr.length > 0 && inputstr.substring(0, 1) === "0") {
            inputstr = inputstr.slice(1)
        }
        if (inputstr.length === 0) {
            inputstr = "0"
        }
        inputstr = "0x" + inputstr
        inputstr = inputstr.substring(0, 10) // cut off too many characters
        const as_int = parseInt(inputstr.substring(2), 16)
        setNewSeed(as_int)
    }

    const loadAbilityHandler = event => {
        setLoadAbility(event)
    }

    const abilityOverwriteHandler = (position, ability) => {
        if (position === 0) {
            setNewMain(getSkillName(ability))
        } else {
            const pos = ((position - 1) / 2) >>> 0
            if (position & 1) {
                // ability
                if (newHistory.length === pos) {
                    setNewHistory(currentHistory => {
                        return [...currentHistory, ability]
                    })
                    setNewDrinks(currentDrinks => {
                        return [...currentDrinks, 255]
                    })
                } else {
                    const new_history = [...newHistory]
                    const new_drinks = [...newDrinks]
                    new_history[pos] = ability
                    // clean up
                    while (new_history[new_history.length - 1] === 255 && new_drinks[new_drinks.length - 1] === 255) {
                        new_history.splice(new_history.length - 1, 1)
                        new_drinks.splice(new_drinks.length - 1, 1)
                    }
                    setNewHistory(new_history)
                    setNewDrinks(new_drinks)
                }
            } else {
                // drink
                if (newHistory.length === pos) {
                    setNewHistory(currentHistory => {
                        return [...currentHistory, 255]
                    })
                    setNewDrinks(currentDrinks => {
                        return [...currentDrinks, ability]
                    })
                } else {
                    const new_history = [...newHistory]
                    const new_drinks = [...newDrinks]
                    new_drinks[pos] = ability
                    //cleanup
                    while (new_history[new_history.length - 1] === 255 && new_drinks[new_drinks.length - 1] === 255) {
                        new_history.splice(new_history.length - 1, 1)
                        new_drinks.splice(new_drinks.length - 1, 1)
                    }
                    setNewHistory(new_history)
                    setNewDrinks(new_drinks)
                }
            }
        }
        setLoadAbility(-1)
    }

    const deleteData = useCallback(() => {
        if (!window.confirm(t("misc:Database.DeleteConfirmation"))) return

        const gearKey = newType === 0 ? "HaveGearHeadMap" : newType === 1 ? "HaveGearClothesMap" : "HaveGearShoesMap"
        const new_dict = { ...gearList }
        if (newGearId in new_dict[gearKey]) {
            delete new_dict[gearKey][newGearId]
        }
        setGearList(new_dict)
        cancelGearEdit()
    }, [setGearList, gearList, newGearId, newType, t])

    const generateSeedHandler = () => {
        const gearKey = newType === 0 ? "Head" : newType === 1 ? "Clothes" : "Shoes"
        const new_seed = MurmurHash3.x86.hash32(`${decrypted_key()}_${gearKey}_${newGearId}`) >>> 0
        setNewSeed(new_seed)
    }

    return (
        <>
            <h1>Splatoon 3 - {t("misc:Navigation.GearDatabase")}</h1>
            <div className={classes.SettingsContent}>
                <div className={classes.Wrapper}>
                    <Suspense fallback={false}>
                        <GearEditor
                            show={newGear}
                            onHide={() => cancelGearEdit()}
                            onSave={add_new_gear_handler}
                            onDelete={deleteData}
                            currentGearList={gearList}
                            newGearId={newGearId}
                            setNewGearId={setNewGearId}
                            newType={newType}
                            setNewGearType={setNewGearType}
                            newMain={newMain}
                            setNewMain={setNewMain}
                            newSeed={newSeed}
                            setNewSeed={setNewSeed}
                            newGearHandler={newGearHandler}
                            seedValidator={checkSeedHandler}
                            loadAbility={loadAbility}
                            loadAbilityHandler={loadAbilityHandler}
                            abilityOverwriteHandler={abilityOverwriteHandler}
                            history={newHistory}
                            historyDrinks={newDrinks}
                            keyset={valid_key() && newSeed === 0}
                            generateSeedHandler={generateSeedHandler}
                        />
                    </Suspense>
                    <div className={classes.Card} onClick={() => setNewGear(true)}>
                        <div className={classes.CardDark}>
                            <div className={classes.CardContent}>{t("misc:Database.AddNewGear")}</div>
                            <div className={classes.CardBox}>
                                <button className={classes.Button}>
                                    <div className={classes.ButtonH1}>
                                        <FontAwesomeIcon icon={faPlus} className="fa-fw" />
                                    </div>
                                </button>
                            </div>
                        </div>
                    </div>
                    {Object.entries(gearList.HaveGearHeadMap).map(([k, v], idx) => {
                        return (
                            <Gear
                                key={`${k}-0`}
                                history={v.ExSkillArray}
                                historyDrinks={v.ExDrinksArray}
                                seed={v.RandomContext}
                                main={v.MainSkill}
                                type={0}
                                gear_id={parseInt(k)}
                                onClick={set_new_gear_handler}
                            />
                        )
                    })}
                    {Object.entries(gearList.HaveGearClothesMap).map(([k, v], idx) => {
                        return (
                            <Gear
                                key={`${k}-1`}
                                history={v.ExSkillArray}
                                historyDrinks={v.ExDrinksArray}
                                seed={v.RandomContext}
                                main={v.MainSkill}
                                type={1}
                                gear_id={parseInt(k)}
                                onClick={set_new_gear_handler}
                            />
                        )
                    })}
                    {Object.entries(gearList.HaveGearShoesMap).map(([k, v], idx) => {
                        return (
                            <Gear
                                key={`${k}-2`}
                                history={v.ExSkillArray}
                                historyDrinks={v.ExDrinksArray}
                                seed={v.RandomContext}
                                main={v.MainSkill}
                                type={2}
                                gear_id={parseInt(k)}
                                onClick={set_new_gear_handler}
                            />
                        )
                    })}
                </div>
            </div>
        </>
    )
}

export default Database
