import classes from "./SeedLister.module.css"
import Control from "./Salmonrun/Control"
import ResultSet from "./Salmonrun/ResultSet"
import { useEffect, useState } from "react"
import { defaultSchedule, defaultSRGear } from "../../DefaultEntries"
import { useLocalStorage, decrypted_key, SkillIDsRev } from "../../Utils"
import XORSHIFT128 from "./RNGLib"
import CoopReward from "../../assets/json/parameters/spl__CoopRewardConfig.spl__CoopRewardConfig.json"
import MurmurHash3 from "murmurhash3js"
import { next } from "../SeedChecker/SeedCalculator"
import { useTranslation } from "react-i18next"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faMartiniGlassEmpty } from "@fortawesome/free-solid-svg-icons"

const RewardCapsules = {
    Money: <img src={"https://leanny.github.io/splat3/images/capsules/CapsuleIcon_00.png"} alt={"Money"} width={48} />,
    Food: <img src={"https://leanny.github.io/splat3/images/capsules/CapsuleIcon_01.png"} alt={"Food"} width={48} />,
    Drink: <img src={"https://leanny.github.io/splat3/images/capsules/CapsuleIcon_02.png"} alt={"Drink"} width={48} />,
    GearSkillChip: (
        <img
            src={"https://leanny.github.io/splat3/images/capsules/CapsuleIcon_03.png"}
            alt={"GearSkillChip"}
            width={48}
        />
    ),
    GearRandom: (
        <img src={"https://leanny.github.io/splat3/images/capsules/CapsuleIcon_04.png"} alt={"GearRandom"} width={48} />
    ),
    GearMonthly: "",
}

const Salmonrun = props => {
    const [scheduleData, setScheduleData] = useLocalStorage("schedule", defaultSchedule)
    const [SRGearData, setSRGearData] = useLocalStorage("SRGear", defaultSRGear)

    const format_date_string = val => {
        return val.split(":")[0].replace("T", "").replaceAll("-", "")
    }

    const [currentStartTime, setCurrentStartTime] = useState(format_date_string(new Date().toUTCString()))
    const [currentEndTime, setCurrentEndTime] = useState(new Date())
    const [currentNumberOfFrames, setCurrentNumberOfFrames] = useState(12)
    const [results, setResults] = useState([])
    const { t } = useTranslation()

    const convert_reward = res => {
        let resElem
        for (let k in CoopReward.CapsuleInside) {
            const elem = CoopReward.CapsuleInside[k]
            if (elem.CapsuleRarity === res.RewardRarity && elem.CapsuleType === res.RewardType) {
                resElem = elem
                break
            }
        }
        if (!resElem) {
            const r = [<img key={res.CapsuleType} src={res.RewardImage} alt={res.CapsuleType} width={48} />]
            let rand = next(res.Random)
            r.push(
                <img
                    src={`https://leanny.github.io/splat3/images/skill/${SkillIDsRev[rand % 28 >>> 1]}.png`}
                    width={48}
                    alt={SkillIDsRev[rand % 28 >>> 1]}
                    className={classes.Ability}
                    key={rand}
                />
            )
            for (let i = 2; i <= res.RewardRarityNum; i++) {
                rand = next(rand)
                r.push(
                    <img
                        src={`https://leanny.github.io/splat3/images/skill/${SkillIDsRev[rand % 28 >>> 1]}.png`}
                        width={32}
                        alt={SkillIDsRev[rand % 28 >>> 1]}
                        className={classes.Ability}
                        key={rand}
                    />
                )
            }
            return r
        }

        if (resElem.IsMoney) {
            return (
                <>
                    <img
                        src={"https://leanny.github.io/splat3/images/misc/GiftMoneyIcon_00.png"}
                        width={48}
                        alt={resElem.Value}
                    />
                    {resElem.Value}
                </>
            )
        }
        if (resElem.CapsuleType === "Drink") {
            let drinks = []
            let r = res.Random
            for (let i = 0; i < resElem.Value; i++) {
                r = next(r)
                drinks.push(
                    <img
                        src={`https://leanny.github.io/splat3/images/skill/${SkillIDsRev[r % 14]}.png`}
                        width={48}
                        alt={SkillIDsRev[r % 14]}
                        className={classes.Ability}
                        key={r}
                    />
                )
            }
            return (
                <>
                    <FontAwesomeIcon icon={faMartiniGlassEmpty} /> {drinks}
                </>
            )
        }
        if (resElem.CapsuleType === "Food") {
            let desc
            if (resElem.Ticket === "Money50") {
                desc = (
                    <>
                        <img
                            src={"https://leanny.github.io/splat3/images/food/IconShopFood.png"}
                            alt={resElem.Ticket}
                            width={48}
                        />{" "}
                        (+ 50%)
                    </>
                )
            }
            if (resElem.Ticket === "Money100") {
                desc = (
                    <>
                        <img
                            src={"https://leanny.github.io/splat3/images/food/IconShopFoodL.png"}
                            alt={resElem.Ticket}
                            width={48}
                        />{" "}
                        (+ 100%)
                    </>
                )
            }
            if (resElem.Ticket === "MoneyParty") {
                desc = (
                    <img
                        src={"https://leanny.github.io/splat3/images/food/IconShopParty_02.png"}
                        alt={resElem.Ticket}
                        width={48}
                    />
                )
            }
            return (
                <>
                    {resElem.Value} x {desc}
                </>
            )
        }
        if (resElem.CapsuleType === "GearSkillChip") {
            const chip = SkillIDsRev[next(res.Random) % 14]
            return (
                <>
                    {resElem.Value} x{" "}
                    <img
                        src={`https://leanny.github.io/splat3/images/skill/${chip}.png`}
                        width={48}
                        alt={chip}
                        className={classes.Ability}
                    />{" "}
                    ({t("translation:LayoutMsg.Cmn_Menu_00.T_Count_01")})
                </>
            )
        }
        return ""
    }

    const checkNumberOfFrameHandler = event => {
        let inputstr = event.target.value
        inputstr = inputstr.replace(/[^0-9]/g, "")
        while (inputstr.length > 0 && inputstr.substring(0, 1) === "0") {
            inputstr = inputstr.slice(1)
        }
        if (inputstr.length === 0 || inputstr === "0") {
            inputstr = "1"
        }
        inputstr = inputstr.substring(0, 3) // cut off too many characters
        setCurrentNumberOfFrames(inputstr)
    }

    const startTimeHandler = event => {
        setCurrentStartTime(
            format_date_string(
                scheduleData.data.coopGroupingSchedule.regularSchedules.nodes[event.target.value].startTime
            )
        )
        setCurrentEndTime(scheduleData.data.coopGroupingSchedule.regularSchedules.nodes[event.target.value].endTime)
    }

    const get_xorshift = () => {
        return new XORSHIFT128(
            MurmurHash3.x86.hash32(`Coop_0_${decrypted_key()}_${currentStartTime}`) >>> 0,
            MurmurHash3.x86.hash32(`Coop_1_${decrypted_key()}_${currentStartTime}`) >>> 0,
            MurmurHash3.x86.hash32(`Coop_2_${decrypted_key()}_${currentStartTime}`) >>> 0,
            MurmurHash3.x86.hash32(`Coop_3_${decrypted_key()}_${currentStartTime}`) >>> 0
        )
    }

    const calculatePoints = () => {
        const points = [...CoopReward.RewardMeterNomal.ElementArray]
        for (let i = 0; i < CoopReward.RewardMeterNomal.RepearElementArray[0].RepeatNum; i++) {
            points.push({
                KumaScale: 14 + i * CoopReward.RewardMeterNomal.RepearElementArray[0].KumaScaleSpan,
                RewardMeterElementType: CoopReward.RewardMeterNomal.RepearElementArray[0].RewardMeterElementType,
            })
        }
        for (let i = 0; i < CoopReward.RewardMeterNomal.RepearElementArray[1].RepeatNum; i++) {
            points.push({
                KumaScale: 16 + i * CoopReward.RewardMeterNomal.RepearElementArray[1].KumaScaleSpan,
                RewardMeterElementType: CoopReward.RewardMeterNomal.RepearElementArray[1].RewardMeterElementType,
            })
        }
        for (let i = 0; i < 487; i++) {
            points.push({
                KumaScale: 40 + i * CoopReward.RewardMeterNomal.RepearElementArray[2].KumaScaleSpan,
                RewardMeterElementType: CoopReward.RewardMeterNomal.RepearElementArray[2].RewardMeterElementType,
            })
        }
        for (let i = 0; i < 487; i++) {
            points.push({
                KumaScale: 44 + i * CoopReward.RewardMeterNomal.RepearElementArray[3].KumaScaleSpan,
                RewardMeterElementType: CoopReward.RewardMeterNomal.RepearElementArray[3].RewardMeterElementType,
            })
        }
        return points.sort((a, b) => a.KumaScale - b.KumaScale)
    }

    const evaluate = res => {
        return (
            <div className={classes.FlexElem2}>
                {RewardCapsules[res.RewardType]} {convert_reward(res)}
            </div>
        )
    }

    const calculateResults = () => {
        const RewardTypes = ["None", "Unknown", "Money", "Food", "Drink", "GearSkillChip", "GearRandom", "GearMonthly"]
        const RewardRarities = ["None", "Unknown", "Common", "Rare", "VeryRare", "SuperRare"]
        const RewardTypeProbability = RewardTypes.map(e => CoopReward.RandomWeight[e])
        const rng = get_xorshift()
        const int_seed = rng.get_u32()
        const res = []
        const pointsRequired = calculatePoints()
        let rotation_data
        for (const k of Object.keys(SRGearData).sort((a, b) => a > b)) {
            if (rotation_data === undefined || k <= currentEndTime) {
                rotation_data = SRGearData[k]
            }
        }
        const current_gear = rotation_data.current
        for (let i = 0; i < parseInt(currentNumberOfFrames); i++) {
            const seed = new XORSHIFT128(((int_seed * pointsRequired[i].KumaScale) & 0xffffffff) >>> 0, 0)
            let cumProb = 0
            let RewardType = 0
            let RewardRarity = 0
            let RewardImage
            if (pointsRequired[i].RewardMeterElementType === "MonthlyGear") {
                RewardType = 7
                RewardImage = current_gear
            } else if (pointsRequired[i].RewardMeterElementType === "UsedGearCapsule") {
                RewardType = 6
            } else {
                for (let k = 0; k < RewardTypes.length; k++) {
                    if (RewardTypeProbability[k] > 0) {
                        cumProb += RewardTypeProbability[k]
                        const x = seed.get_rand_int(0, cumProb)
                        if (RewardTypeProbability[k] > x) {
                            RewardType = k
                        }
                    }
                }
            }
            if (RewardType === 6) {
                let res = 0
                for (let i = 0; i < rotation_data.past.length; i++) {
                    if (seed.get_rand_int(0, i + 1) === 0) {
                        res = i
                    }
                }
                RewardImage = rotation_data.past[res]
            }

            if (pointsRequired[i].RewardMeterElementType === "RandomHit") {
                RewardRarity = 4
            } else {
                const RarityWeight = RewardRarities.map(e => CoopReward.RarityWeight[RewardTypes[RewardType]].Weight[e])
                cumProb = 0
                for (let k = 0; k < RewardRarities.length; k++) {
                    if (RarityWeight[k] > 0) {
                        cumProb += RarityWeight[k]
                        const x = seed.get_rand_int(0, cumProb)
                        if (RarityWeight[k] > x) {
                            RewardRarity = k
                        }
                    }
                }
            }

            res.push({
                Idx: pointsRequired[i].KumaScale * 100,
                Result: evaluate({
                    RewardType: RewardTypes[RewardType],
                    RewardRarity: RewardRarities[RewardRarity],
                    Random: seed.get_u32(),
                    RewardRarityNum: RewardRarity,
                    RewardImage: RewardImage,
                }),
            })
        }
        setResults(res)
    }

    useEffect(() => {
        if (
            new Date(
                (
                    scheduleData.data.coopGroupingSchedule.regularSchedules.nodes[0] ?? {
                        endTime: new Date().toUTCString(),
                    }
                ).endTime
            ) <= Date.now()
        ) {
            fetch("https://splatoon3.ink/data/schedules.json")
                .then(response => response.json())
                .then(response => {
                    setScheduleData(response)
                    setCurrentStartTime(
                        format_date_string(response.data.coopGroupingSchedule.regularSchedules.nodes[0].startTime)
                    )
                    setCurrentEndTime(response.data.coopGroupingSchedule.regularSchedules.nodes[0].endTime)
                })
        } else {
            setCurrentStartTime(
                format_date_string(scheduleData.data.coopGroupingSchedule.regularSchedules.nodes[0].startTime)
            )
            setCurrentEndTime(scheduleData.data.coopGroupingSchedule.regularSchedules.nodes[0].endTime)
        }
    }, [setScheduleData, scheduleData, setCurrentStartTime])

    useEffect(() => {
        fetch("https://leanny.github.io/splat3/sr_gear.json")
            .then(response => response.json())
            .then(response => {
                setSRGearData(response)
            })
    }, [setSRGearData])

    return (
        <>
            <Control
                scheduleData={scheduleData.data.coopGroupingSchedule.regularSchedules.nodes}
                selectChange={startTimeHandler}
                selectValue={currentStartTime}
                inputChange={checkNumberOfFrameHandler}
                inputValue={currentNumberOfFrames}
                calculateResults={calculateResults}
                format_date_string={format_date_string}
            />
            {<ResultSet results={results} />}
        </>
    )
}

export default Salmonrun
