import { useLocalStorage, decrypted_key, SkillIDsRev } from "../../Utils"
import Control from "./Gacha/Control"
import React, { useState, useCallback } from "react"
import ResultSet from "./Gacha/Result"
import XORSHIFT128 from "./RNGLib"
import LotReward from "../../assets/json/parameters/LotRewardData.json"
import classes from "./SeedLister.module.css"
import GachaInit from "./Gacha/GachaInit"
import ControlInit from "./Gacha/ControlInit"
import ResultInit from "./Gacha/ResultInit"
import { defaultImportData } from "../../DefaultEntries"
import { get_reward_types } from "../../Utils"
import { useTranslation } from "react-i18next"

export const format_grade = entry => {
    return (
        <div className={classes.container}>
            <img
                className={classes.upper}
                src={`https://leanny.github.io/splat3/images/capsules/LobbyCapsule_Top_0${entry.Grade}.png`}
                alt={entry.Grade}
            />
            <img
                className={classes.lower}
                src={`https://leanny.github.io/splat3/images/capsules/LobbyCapsule_Bottom_0${entry.Grade}.png`}
                alt={entry.Grade}
            />
        </div>
    )
}

const Gacha = props => {
    const [userData, setUserData] = useLocalStorage("importData", defaultImportData)
    const { t } = useTranslation()
    const reward_types = get_reward_types(t)

    const to_hex = x => `0x${(x >>> 0).toString(16)}`

    const init_current_seed = () => {
        if (userData.CloudRandomSet === undefined) {
            const rng = new XORSHIFT128(`${decrypted_key()}_${props.localKey}`)
            return rng.get_state()
        }
        const inital_seed = userData.CloudRandomSet.Map[props.localKey]
        return [
            to_hex(inital_seed.Context1),
            to_hex(inital_seed.Context2),
            to_hex(inital_seed.Context3),
            to_hex(inital_seed.Context4),
        ]
    }

    const [currentSeed, setCurrentSeed] = useState(init_current_seed())
    const [currentStartFrame, setCurrentStartFrame] = useState(1)
    const [currentNumberOfFrames, setCurrentNumberOfFrames] = useState(10)
    const [displayResults, setDisplayResults] = useState([])
    const [selectedResultTypes, setSelectedResultTypes] = useState(reward_types)
    const [possibleResults, setPossibleResults] = useState([])

    const initialised =
        userData.CloudRandomSet !== undefined && userData.CloudRandomSet.Map[props.localKey].Context1 > 0

    const handleResultTypes = e => {
        setSelectedResultTypes(e)
    }

    const checkSeedHandler = (event, idx) => {
        let inputstr = event.target.value.toUpperCase()
        if (inputstr.substring(0, 2) === "0X") {
            inputstr = inputstr.substring(2)
        }
        inputstr = inputstr.replace(/[^0-9A-F]/g, "")
        while (inputstr.length > 0 && inputstr.substring(0, 1) === "0") {
            inputstr = inputstr.slice(1)
        }
        if (inputstr.length === 0) {
            inputstr = "0"
        }
        inputstr = "0x" + inputstr
        inputstr = inputstr.substring(0, 10) // cut off too many characters
        const old_input = [...currentSeed]
        old_input[idx] = inputstr
        setCurrentSeed(old_input)
    }

    const checkStartFrameHandler = event => {
        let inputstr = event.target.value
        inputstr = inputstr.replace(/[^0-9]/g, "")
        while (inputstr.length > 0 && inputstr.substring(0, 1) === "0") {
            inputstr = inputstr.slice(1)
        }
        if (inputstr.length === 0 || inputstr === "0") {
            inputstr = "1"
        }

        inputstr = inputstr.substring(0, 5) // cut off too many characters
        setCurrentStartFrame(inputstr)
    }

    const checkNumberOfFrameHandler = event => {
        let inputstr = event.target.value
        inputstr = inputstr.replace(/[^0-9]/g, "")
        while (inputstr.length > 0 && inputstr.substring(0, 1) === "0") {
            inputstr = inputstr.slice(1)
        }
        if (inputstr.length === 0 || inputstr === "0") {
            inputstr = "1"
        }
        inputstr = inputstr.substring(0, 5) // cut off too many characters
        setCurrentNumberOfFrames(inputstr)
    }

    const updateSeedHandler = (newSeed, storeRes) => {
        setCurrentSeed(newSeed)
        if (storeRes) {
            const original = { ...userData }
            original.CloudRandomSet.Map[props.localKey].Context1 = parseInt(newSeed[0].substring(2), 16)
            original.CloudRandomSet.Map[props.localKey].Context2 = parseInt(newSeed[1].substring(2), 16)
            original.CloudRandomSet.Map[props.localKey].Context3 = parseInt(newSeed[2].substring(2), 16)
            original.CloudRandomSet.Map[props.localKey].Context4 = parseInt(newSeed[3].substring(2), 16)
            setUserData(original)
        }
    }

    const get_res = rng_res => {
        const LotInfo = LotReward[0].Info
        let val = 0.0
        let i = -1
        while (val <= rng_res) {
            i += 1
            val += LotInfo[i].Percent
        }
        return LotInfo[i]
    }

    const evaluate_result = rng => {
        const res = get_res(rng.get_rand_float(0.0, 100.0))
        if (res.SubType === "Limited") {
            rng.get_u32()
            rng.get_u32()
            return res
        }
        if (res.Type === "FoodTicket") {
            rng.get_u32()
            return res
        }
        if (res.Type === "GearSkillChip" || res.Type === "DrinkTicket") {
            const ability = (rng.get_u32() >>> 0) % 14
            res.Ability = ability
            return res
        }
        return res
    }

    const format_result = entry => {
        let desc = ""
        if (entry.Type === "MiniGameCardPack") {
            desc = <img src={"https://leanny.github.io/splat3/images/catalog/IconCardPack_00.png"} alt={entry.Type} />
        }
        if (entry.Type === "FoodTicket") {
            if (entry.SubType === "Food50") {
                desc = <img src={"https://leanny.github.io/splat3/images/food/IconShopFoodRank.png"} alt={entry.Type} />
            }
            if (entry.SubType === "Food100") {
                desc = (
                    <img src={"https://leanny.github.io/splat3/images/food/IconShopFoodRankL.png"} alt={entry.Type} />
                )
            }
            if (entry.SubType === "FoodParty") {
                desc = <img src={"https://leanny.github.io/splat3/images/food/IconShopParty_01.png"} alt={entry.Type} />
            }
        }
        if (entry.Type === "NamePlateBg") {
            desc = <img src={"https://leanny.github.io/splat3/images/catalog/IconNameplate_02.png"} alt={entry.Type} />
        }
        if (entry.Type === "Byname") {
            desc = <img src={"https://leanny.github.io/splat3/images/catalog/IconNameplate_03.png"} alt={entry.Type} />
        }
        if (entry.SubType === "Limited") {
            if (entry.Type === "NamePlateBg") {
                desc = (
                    <img
                        src={"https://leanny.github.io/splat3/images/catalog/IconNameplate_02.png"}
                        alt={entry.Type}
                        className={classes.Limited}
                    />
                )
            }
            if (entry.Type === "Byname") {
                desc = (
                    <img
                        src={"https://leanny.github.io/splat3/images/catalog/IconNameplate_03.png"}
                        alt={entry.Type}
                        className={classes.Limited}
                    />
                )
            }
        }
        if (entry.Type === "GearSkillChip") {
            desc = (
                <div className={classes.FlexElem}>
                    {t("translation:LayoutMsg.Cmn_Menu_00.T_Count_01")}{" "}
                    <img
                        src={`https://leanny.github.io/splat3/images/skill/${SkillIDsRev[entry.Ability]}.png`}
                        alt={entry.Type}
                        className={classes.Chunk}
                        height={64}
                    />{" "}
                    x {entry.SameNum}
                </div>
            )
        }
        if (entry.Type === "DrinkTicket") {
            desc = (
                <div className={classes.FlexElem}>
                    {t("other:Extra.Drink")}{" "}
                    <img
                        src={`https://leanny.github.io/splat3/images/skill/${SkillIDsRev[entry.Ability]}.png`}
                        alt={entry.Type}
                        className={classes.Chunk}
                        height={64}
                    />{" "}
                    x {entry.SameNum}
                </div>
            )
        }
        if (entry.Type === "LockerSticker") {
            desc = (
                <div className={classes.FlexElem}>
                    {entry.NotSameNum} x {t("translation:CommonMsg.Glossary.Sticker")}
                </div>
            )
        }
        if (entry.Type === "LockerFigure") {
            desc = (
                <div className={classes.FlexElem}>
                    {entry.NotSameNum} x {t("translation:CommonMsg.Glossary.Figure")}
                </div>
            )
        }
        if (entry.Type === "AllGearSkillChip") {
            desc = (
                <div className={classes.FlexElem}>
                    {t("translation:LayoutMsg.Cmn_Menu_00.T_Count_01")} x {entry.SameNum}
                </div>
            )
        }
        return (
            <div className={classes.ImgRes}>
                <center>{desc}</center>
            </div>
        )
    }

    const format_state = state => {
        return (
            <>
                {state[0]} | {state[1]} | {state[2]} | {state[3]}
            </>
        )
    }

    const is_shown = elem => {
        if (selectedResultTypes.length === 0) return true
        if (elem.SubType === "Limited") {
            if (elem.Type === "NamePlateBg") {
                for (const value of selectedResultTypes) {
                    if (value.value === "LimitedNamePlateBg") {
                        return true
                    }
                }
            } else if (elem.Type === "Byname") {
                for (const value of selectedResultTypes) {
                    if (value.value === "LimitedByname") {
                        return true
                    }
                }
            }
            for (const value of selectedResultTypes) {
                if (value.value === elem.Type) {
                    return true
                }
            }
        } else {
            for (const value of selectedResultTypes) {
                if (value.value === elem.Type || (elem.SubType !== "None" && value.value === elem.SubType)) {
                    return true
                }
            }
        }

        return false
    }

    const calculateResultHandler = () => {
        const rng = new XORSHIFT128(currentSeed[0], currentSeed[1], currentSeed[2], currentSeed[3])
        const res = []
        let i = 0
        const startFrame = parseInt(currentStartFrame) - 1
        const maxFrames = parseInt(currentNumberOfFrames)
        for (i = 0; i < startFrame; i++) {
            evaluate_result(rng)
        }
        for (let results = 0, i = 0; results < maxFrames; i++) {
            const tmp = evaluate_result(rng)
            if (is_shown(tmp)) {
                res.push({
                    Idx: startFrame + i + 1,
                    Result: format_result(tmp),
                    Grade: format_grade(tmp),
                    Raw: tmp,
                    Seed: format_state(rng.get_state()),
                    RawSeed: rng.get_state(),
                    Update: true,
                })
                results = results + 1
            }
        }
        setDisplayResults(res)
    }

    const seed_fits = (current_state, entries) => {
        for (var entry of entries) {
            const r = evaluate_result(current_state)
            if (r.Type !== entry.Value.original.Type || r.SubType !== entry.Value.original.SubType) {
                return false
            }
        }
        return true
    }

    const find_seed = entries => {
        const initial_seed = new XORSHIFT128(`${decrypted_key()}_${props.localKey}`)
        const potential_seeds = []
        for (var i = 0; i < 100000; i++) {
            const current_state = initial_seed.get_clone()
            if (seed_fits(current_state, entries)) {
                potential_seeds.push(current_state.get_clone())
            }
            initial_seed.get_u32()
        }
        setPossibleResults(potential_seeds)
    }

    const store_seed_from_finding = () => {
        if (possibleResults.length === 1) {
            const original = { ...userData }
            const newSeed = possibleResults[0].get_raw_state()
            original.CloudRandomSet.Map[props.localKey].Context1 = newSeed[0]
            original.CloudRandomSet.Map[props.localKey].Context2 = newSeed[1]
            original.CloudRandomSet.Map[props.localKey].Context3 = newSeed[2]
            original.CloudRandomSet.Map[props.localKey].Context4 = newSeed[3]
            setUserData(original)
            setCurrentSeed(possibleResults[0].get_state())
        }
    }

    const resetSeed = useCallback(() => {
        if (!window.confirm(t("misc:Common.DeleteConfirmation"))) return

        const original = { ...userData }
        original.CloudRandomSet.Map[props.localKey].Context1 = 0
        original.CloudRandomSet.Map[props.localKey].Context2 = 0
        original.CloudRandomSet.Map[props.localKey].Context3 = 0
        original.CloudRandomSet.Map[props.localKey].Context4 = 0
        setUserData(original)
    }, [userData, t, setUserData, props.localKey])

    if (!initialised) {
        return (
            <>
                <GachaInit />
                <ControlInit findSeed={find_seed} />
                <ResultInit possibleResults={possibleResults} storeRes={store_seed_from_finding} />
            </>
        )
    }

    return (
        <>
            <Control
                seedValidator={checkSeedHandler}
                currentSeed={currentSeed}
                currentStartFrame={currentStartFrame}
                currentNumberOfFrames={currentNumberOfFrames}
                startValidator={checkStartFrameHandler}
                numberValidator={checkNumberOfFrameHandler}
                calculateResults={calculateResultHandler}
                handleResultTypes={handleResultTypes}
                selectedResultTypes={selectedResultTypes}
                resetSeed={resetSeed}
            />
            <ResultSet results={displayResults} updateSeed={updateSeedHandler} />
        </>
    )
}

export default Gacha
