import React, { useState } from "react"
import Tabs from "./Tabs"
import Gacha from "./Gacha"
import { valid_key, hasContext } from "../../Utils"
import classes from "./SeedLister.module.css"
import { NavLink } from "react-router-dom"
import { useTranslation } from "react-i18next"
import Salmonrun from "./Salmonrun"

const SeedLister = props => {
    const [activeTab, setActiveTab] = useState(0)
    const { t } = useTranslation()

    if (!valid_key() && !hasContext()) {
        return (
            <>
                <h1>Splatoon 3 - {t("misc:Navigation.SeedListing")}</h1>
                <div className={classes.WrapperRound}>
                    <p>
                        <NavLink to="/settings" end>
                            In order to use this feature, you need to import your data from SplatNet first.
                        </NavLink>
                    </p>
                </div>
            </>
        )
    }

    const changeTabHandler = val => {
        setActiveTab(val)
    }

    return (
        <>
            <h1>Splatoon 3 - {t("misc:Navigation.SeedListing")}</h1>
            <Tabs clicked={changeTabHandler} active={activeTab} />
            {activeTab === 0 ? (
                <>
                    <Gacha key="Lot" localKey="Lot" />
                </>
            ) : activeTab === 1 ? (
                <>
                    <Gacha key="LotFest" localKey="LotFest" />
                </>
            ) : activeTab === 2 ? (
                <>
                    <Salmonrun />
                </>
            ) : null}
        </>
    )
}

export default SeedLister

