import React, { Suspense, useState } from "react"
import Layout from "./containers/Layout/Layout"
import SeedChecker from "./containers/SeedChecker/SeedChecker"
import SeedRecover from "./containers/SeedRecover/SeedRecover"
import Tutorial from "./containers/Tutorial/Tutorial"
import Writeup from "./containers/Writeup/Writeup"
import Database from "./containers/Database/Database"
import Usersettings from "./containers/Usersettings/Usersettings"
import SeedLister from "./containers/SeedLister/SeedLister"
import { HashRouter, Route, Routes } from "react-router-dom"

function App() {
    const [render, setRender] = useState(false)

    const render_again = () => {
        setRender(prev => !prev)
    }

    return (
        <Suspense fallback="loading">
            <HashRouter basename="/">
                <Layout key={render} rerender={render}>
                    <Routes>
                        <Route path="/" element={<SeedChecker rerender={render} />} />
                        <Route path="/database" element={<Database rerender={render} />} />
                        <Route path="/seedrecover" element={<SeedRecover rerender={render} />} />
                        <Route path="/seedlisting" element={<SeedLister rerender={render} />} />
                        <Route path="/tutorial" element={<Tutorial rerender={render} />} />
                        <Route path="/writeup" element={<Writeup rerender={render} />} />
                        <Route
                            path="/settings"
                            element={<Usersettings rerender={render} renderAgain={render_again} />}
                        />
                    </Routes>
                </Layout>
            </HashRouter>
        </Suspense>
    )
}

export default App
