export const useLocalStorage = (storageKey, fallbackState) => {
    let jsondata = ""
    try {
        jsondata = JSON.parse(localStorage.getItem(storageKey)) ?? fallbackState
    } catch (e) {
        jsondata = fallbackState
        alert("Gear Database Damaged. Please contact @LeanyYoshi about this. Your Database has been reseted.")
    }
    const [value, _setValue] = useState(jsondata)

    const setValue = useCallback(
        value => {
            _setValue(value)
            localStorage.setItem(storageKey, JSON.stringify(value))
        },
        [storageKey]
    )

    return [value, setValue]
}

export const getSkillId = name => {
    if (name === "Unknown") return 255
    return SkillIDs[name]
}

export const getSkillName = id => {
    if (id === 255) return "Unknown"
    for (const [k, v] of Object.entries(SkillIDs)) {
        if (v === id) return k
    }
}

export const getName = (t, v, gear) => t("translation:" + v + "." + gear.__RowId.substring(4))

export const skillImage = name => `https://leanny.github.io/splat3/images/skill/${name}.png`
export const brandImage = name => `https://leanny.github.io/splat3/images/brand/${name}.png`
export const gearImage = name => `https://leanny.github.io/splat3/images/gear/${name}.png`

export function useBoolState(initial = false) {
    const [bool, setBool] = useState(initial)
    const onTrue = useCallback(() => setBool(true), [setBool])
    const onFalse = useCallback(() => setBool(false), [setBool])
    return [bool, onTrue, onFalse]
}

export function decrypt_key(tmp_key, hash) {
    const key = hash & 0xff
    const decrypted = new TextDecoder().decode(Buffer.from(tmp_key, "base64").map(e => e ^ key))
    if (hash >>> 0 !== MurmurHash3.x86.hash32(decrypted) >>> 0) {
        return undefined
    }
    return decrypted
}

export function valid_key() {
    const data = JSON.parse(localStorage.getItem("importData")) ?? {}
    const hash = data.H ?? 0
    return hash > 0 && decrypted_key() !== undefined
}

export function decrypted_key() {
    const data = JSON.parse(localStorage.getItem("importData")) ?? {}
    const tmp_key = data.UserKey ?? ""
    const hash = data.H ?? 0
    return decrypt_key(tmp_key, hash)
}

export function hasContext() {
    const data = JSON.parse(localStorage.getItem("importData")) ?? {}
    const lot1 = data.CloudRandomSet ?? {}
    const lot2 = lot1.Map ?? {}
    const lot3 = lot2.Lot ?? []
    const lot4 = lot3.Context1 ?? 0
    return lot4 > 0
}

export const dict_length = d => Object.keys(d).length

export const get_drink_order = () => {
    return ability_order.map(e => MapingBrandDrink[e])
}

export const drink_order = get_drink_order()

export const generate_abilities = t => {
    const res = [{ value: "None", label: t("misc:Common.None") }]
    for (const ability of ability_order) {
        res.push({ value: ability, label: t("translation:CommonMsg.Gear.GearPowerName." + ability) })
    }
    res.sort((a, b) => {
        if (a.value === "None") return false
        if (b.value === "None") return true
        return a.label > b.label
    })
    return res
}

export const generate_drinks = t => {
    const res = [{ value: "None", label: t("misc:Common.None") }]
    for (const ability of ability_order) {
        res.push({ value: ability, label: t("translation:CommonMsg.Lobby.LobbyFoodName." + MapingBrandDrink[ability]) })
    }
    res.sort((a, b) => {
        if (a.value === "None") return false
        if (b.value === "None") return true
        return a.label > b.label
    })
    return res
}

export const generate_brands = (t, no_default) => {
    const res = []
    if (no_default === undefined) {
        res.push({ value: "None", label: t("misc:Common.None") })
    }
    for (const brand of brand_names) {
        if (brand !== "B18") res.push({ value: brand, label: t("translation:CommonMsg.Gear.GearBrandName." + brand) })
    }
    res.sort((a, b) => {
        if (a.value === "None") return false
        if (b.value === "None") return true
        return a.label > b.label
    })
    return res
}

